/* Layout general */
body, .app {
  background: #0c243d;
  color: #fff;
  font-family: 'Segoe UI', sans-serif;
  margin: 0;
  padding: 0;
}
/* HEADER*/
.header-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0c243d;
  padding: 10px 16px;
  height: auto;
}

.logo {
  height: 35px;
  object-fit: contain;
}

.tagline {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  color: #d6b86a;
  white-space: nowrap;
}

.divider {
  width: 2px;
  height: 28px;
  background-color: #d6b86a;
}

/* Navbar */
.nav-bar {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background: #2f3d5e;
  padding: 8px 6px;
  gap: 12px;
  font-size: 13px;
}
.nav-bar button {
  background: none;
  color: #bbb;
  border: none;
  font-size: 13px;
  padding: 4px 6px;
  cursor: pointer;
}
.nav-bar button.active {
  color: #d6b86a;
  font-weight: 600;
}

/* Títulos */
.recommendations h2,
.section h2 {
  padding: 10px 16px;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 6px;
}

/* Recomendaciones */
.recommendations {
  position: relative;
  background-color: #0c243d;
  overflow: hidden;
}

/* Imagen degradada desde abajo */
.recommendations::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40%;
  background: 
    linear-gradient(to top, transparent, rgba(12, 36, 61, 0.6), #0c243d),
    url('../public/assets/bg-image.jpg') no-repeat bottom center / cover;
  pointer-events: none;
  z-index: 0;
}


/* Eleva contenido por encima del degradado */
.recommendations > * {
  position: relative;
  z-index: 1;
}

.dish-card {
  display: flex;
  background: #2f3d5e;
  margin: 10px 16px;
  padding: 10px;
  border-radius: 16px;
  gap: 12px;
  align-items: center;
}
.dish-card img {
  width: 58px;
  height: 100%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  object-fit: cover;
}
.card-content {
  flex: 1;
}
.card-content h4 {
  margin: 0;
  color: #d6b86a;
  font-size: 15px;
}
.card-content p {
  margin: 4px 0;
  color: #ddd;
  font-size: 13px;
  line-height: 1.2;
}
.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 6px;
}
.price {
  font-size: 14px;
  color: #ccc;
}
p{
   margin: 10px 16px;
}
.emotive-quote {
  text-align: center;
  margin-top: 16px;
  font-style: italic;
  color: #aaa;
  margin: 10px 16px;
}
/* Botón AR */
.ar-btn {
  background: #f15454;
  color: white;
  padding: 5px 8px;
  font-size: 10px;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  cursor: pointer;
  margin-left: 10px;
}
.ar-btn:hover {
  background: #d94141;
}

/* Secciones normales */
.subsection h3 {
  color: #d6b86a;
  margin: 18px 16px 8px;
  font-size: 15px;
}

.dish-item {
  display: flex;
  justify-content: space-between;
  background: #2f3d5e;
  margin: 8px 16px;
  padding: 12px;
  border-radius: 16px;
  align-items: center;
}
.dish-item img {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  object-fit: cover;
  margin-right: 12px;
}
.dish-info {
  flex: 1;
}
.dish-info h4 {
  color: #fff;
  margin: 0;
  font-size: 14px;
}
.dish-info p {
  margin: 2px 0 0;
  color: #eee;
  font-size: 12.5px;
}
